import React, { Component, useEffect } from 'react';
import { Link } from 'react-router-dom';

import {Logo} from '../../logo.js';

import axios from 'axios';

import './login.css';

class LoginForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loginId: '',
            password: '',
        };
    }

    handleInputChange = (event) => {
        const { name, value } = event.target;
        this.setState({ [name]: value });
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const testData = {
            id : document.getElementById('loginId'),
            pw : document.getElementById('password')
        }

        console.log(testData.id);
        axios.post('/login/data',testData).then(
            response => {
            console.log(response.data);
        })
        .catch(err => {
        console.log(err);
        })
        }
        //  


    render() {
        return (
            <div className="containerBox">
                <a href='./'><Logo/></a>
                <h2></h2>
                <div id="loginForm">
                    <form onSubmit={this.handleSubmit}>
                        <label htmlFor="loginId"></label><br/>
                        <input
                            type="text"
                            id="username"
                            name="loginId"
                            placeholder=' '
                            value={this.state.loginId}
                            onChange={this.handleInputChange}
                            required
                        /><p/>
                        <br/>
                        <label htmlFor="password"></label><br/>
                        <input
                            type="password"
                            id="password"
                            name="password"
                            placeholder=' '
                            value={this.state.password}
                            onChange={this.handleInputChange}
                            required
                        /><br/><br/><br/><br/>
                        <button type="submit"></button>
                        <br/><br/><br/>
                        <nav id="bottomNav">
                            <li><a href="/newAccount.html" id="newAccount"></a></li>
                            <li>
                                <a href="/findMyID.html" className="findMy"> </a>
                                <span>  </span>
                                <a href="/findMyPW.html" className="findMy"> </a>
                            </li>
                        </nav>
                    </form>
                </div>
            </div>
        );
    }
}

export default LoginForm;